<template>
  <div class="game-board">
      <div class="tools"></div>
      <div class="main">
        <sidebar @start="start" @over="over" />
        <div class="game">
          <matrixMask />
          <matrix :matrix="matrix" />
        </div>
      </div>
      <div class="info">
      </div>
  </div>
</template>
<script>
import matrix from './matrix'
import sidebar from './sidebar'
import matrixMask from './utils/mask'
//
import { initMatrixSquare, initMatrixMine, initMatrixMineNumber } from '../utils'
import { getLevel } from '../utils/play'
import { level } from '../const'

export default {
  name: 'Board',
  data () {
    return {
        matrix: null
    }
  },
  watch: {
    // ÂàáÊç¢ÈöæÂ∫¶‰ª•ÂêéË¶ÅÈáçÁΩÆÁä∂ÊÄÅÂíåÁü©Èòµ
    storeLevel: {
      handler (level) {
        // reset state
        this.$store.dispatch('resetState')
        this.initMatrix(level)
      }
    }
  },
  computed: {
    storeLevel () {
      return this.$store.state.level
    }
  },
  mounted () {
    // ÂàùÂßãÂåñÊ∏∏ÊàèÁü©Èòµ
    this.initMatrix(this.storeLevel)
  },
  methods: {
    initMatrix (customLevel) {
      customLevel = level[getLevel(customLevel)]
      // static temp
      const isOpenExtends = true
      // ÂàùÂßãÂåñÁü©Èòµ
      let matrixSquare = initMatrixSquare(customLevel)
      // ‰øùÂ≠òÁü©ÈòµÈòµÂàóÂèØÁøªÂºÄÁöÑÊÄªÊï∞ÈáèÔºàÊéíÈô§‰∫ÜÂú∞Èõ∑ÁöÑÂÖ®ÈÉ®Âå∫ÂùóÔºâ,ËØ•Êï∞ÈáèÂ∞ÜÁî®‰∫éÂà§ÂÆöËæìËµ¢
      this.$store.dispatch('setTotalCount', matrixSquare.row * matrixSquare.col - customLevel.mine)
      // ÂàùÂßãÂåñÂú∞Èõ∑
      matrixSquare = initMatrixMine(matrixSquare, customLevel, isOpenExtends)
      // ÂàùÂßãÂåñÊï∞ÂÄºÔºàÂú∞Èõ∑Âë®Âõ¥ÁöÑÊï∞Â≠óÔºâ
      initMatrixMineNumber(matrixSquare.matrix)
      // ÂêØÂä®Áü©Èòµ
      this.matrix = matrixSquare.matrix
    },
    start () {
      // reset state
      this.$store.dispatch('resetState')
      // set the play state
      this.$store.dispatch('setPlayState', true)
      // init matrix
      this.initMatrix(this.storeLevel)
    },
    over () {
       // reset state
      this.$store.dispatch('resetState')
      this.$store.dispatch('setPlayState', false)
      this.initMatrix(this.storeLevel)
    }
  },
  components: {
    matrixMask,
    matrix,
    sidebar,
  }
}
</script>
<style lang="scss" scoped>

.game-board {
  display: flex;
  flex-direction: column;
  user-select: none;

  .tools {

  }

  .main {
    display: inherit;
    
    .game {
      position: relative;
      margin-left: 15px;
    }
  }

  .info {

  }
  
}
</style>