import store from '../store'
 /**
  * ÂàùÂßãÂåñÁ©∫ÈòµÂàóÂíåÊï∞ÊçÆÂØπË±°
  * @export
  * @param {*} {row = 10, col = 10}
  * @returns
  */
 export function initMatrixSquare ({row = 10, col = 10}) {
   // init Matrix
  const matrix = Array.from({ length: row },  (r, ri) => {
    return Array.from({ length: col }, (c, ci) => {
      return {
        row: ri,
        col: ci,
        value: 0,
        round: null,
        isOpen: false,
        isMine: false,
        isFlag: false,
        extends: {
          isStone: false,
          isSymbol: false,
          isUFO: false,
        }
      }
    })
  })
  return {
    matrix,
    row,
    col
  }
 }

 /**
  * ÈÖçÁΩÆÁü©Èòµ‰∏≠ÁöÑÂú∞Èõ∑
  *
  * @export
  * @param {*} matrixSquare
  * @param {*} {mine = 50}
  * @returns
  */
 export function initMatrixMine (matrixSquare, {mine = 50, stone = 0 }, isOpenExtends) {
  if( !matrixSquare ) return

  const getRandomSquare = () => {
    return {
      row: Math.floor(Math.random() * matrixSquare.row ),
      col: Math.floor(Math.random() * matrixSquare.col )
    }
  }
  // generate mines
  while (mine > 0) {
    const { row, col } = getRandomSquare()
    const isMine = !!Math.round(Math.random())
    if (isMine) {
      const square = matrixSquare.matrix[row][col]
      if( !square['isMine'] ) {
        // ËÆæÁΩÆÂΩìÂâçÂå∫Âùó‰∏∫Âú∞Èõ∑Âå∫Âùó
        square['isMine'] = true
        store.dispatch('setBomb', square)
        mine--
      }
    }
  }

  // generate the extends params of the square with the exnteds params
  if ( isOpenExtends ) {
    // generate stones
    while ( stone > 0 ) {
      const { row, col } = getRandomSquare()
      const isStone = !!Math.round(Math.random())
      if( isStone ) {
        const square = matrixSquare.matrix[row][col]
        if( !square.extends.isStone && !square.isMine ){
          square.extends.isStone = true
          stone--
        }
      }
    }
  }

  return matrixSquare
 }

 /**
  * ËÆ°ÁÆóÁü©Èòµ‰∏≠Âú∞Èõ∑Âë®Âõ¥ÁöÑÊï∞Â≠ó
  *
  * @export
  * @param {*} matrix
  */
 export function initMatrixMineNumber (matrix) {
    for( let i = 0; i < matrix.length; i++) {
      const row = matrix[i]
      for( let j = 0; j < row.length; j++) {
        let square = row[j]
        const roundSqure = calcRoundSquare(matrix, square)
        // Â∞ÜÁéØÁªïÂΩìÂâçÂå∫ÂùóÁöÑÊâÄÊúâÂå∫ÂùóÁöÑÂºïÁî®ÈÉΩËµãÂÄºÁªôÂΩìÂâçÂå∫ÂùóÔºåÂÅöÂÖ≥ËÅî
        if (!square.round) square.round = roundSqure
        // Â¶ÇÊûúÂΩìÂâçÂå∫Âùó‰∏çÊòØÂú∞Èõ∑ÔºåÂàôÂ∞ÜÂë®Âõ¥Âå∫Âùó‰∏≠Âê´ÊúâÂú∞Èõ∑ÁöÑÊï∞ÈáèËµãÂÄºÁªôÂΩìÂâçÂå∫Âùó
        if(!square.isMine) {
          const roundSquareHasBombSize = Object.values(roundSqure).filter(v => v.isMine).length
          if( roundSquareHasBombSize ) {
            square.value = roundSquareHasBombSize
          }
        }
      }
    }
 }

 /**
  * ËÆ°ÁÆóÁü©Èòµ‰∏≠Êüê‰∏Ä‰∏™Âå∫ÂùóSquareÂë®Âõ¥ÁöÑÂå∫ÂùóÂπ∂ËøîÂõû
  *
  * @param {*} matrix
  * @param {*} square
  * @returns
  */
 function calcRoundSquare (matrix, square) {
  const {row, col} = square
 
  const upRow = matrix[row - 1]
  const downRow = matrix[row + 1]

  return [
    upRow && matrix[row - 1][col - 1],
    upRow && matrix[row - 1][col],
    upRow && matrix[row - 1][col + 1],
    matrix[row][col - 1],
    matrix[row][col + 1],
    downRow && matrix[row + 1][col - 1],
    downRow && matrix[row + 1][col],
    downRow && matrix[row + 1][col + 1]
  ].filter( v => v)

 }

/**
 * ÈÄíÂΩíËÆ°ÁÆóÂΩìÂâçÂå∫ÂùóÂë®Âõ¥ÊâÄÊúâÂèØÁøªÂºÄÁöÑÂå∫ÂùóÂπ∂Â∞ÜÂÖ∂ËÆæÁΩÆ‰∏∫Â∑≤ÁøªÂºÄÁä∂ÊÄÅ
 *
 * @export
 * @param {*} matrix
 * @param {*} square
 */
export function calcRoundBlank(matrix, square) {
  const { round } = square
  for (let v of Object.values(round)) {
    // ÈúÄË¶ÅÈÄíÂΩíÁøªÂºÄÁöÑÂøÖÈ°ªÊòØÁ©∫Âå∫Âùó && Ê≤°ÊúâÊ†áËÆ∞ && ‰∏çÊòØÂú∞Èõ∑ && Ê≤°ÊúâÁøªÂºÄ
    if (!v.isFlag && !v.isMine && !v.isOpen) {
      //
      v.isOpen = true

      // Â¶ÇÊûúÊòØÁ©∫Âå∫ÂùóÂàôÈÄíÂΩíÁªßÁª≠Áøª
      if( v.value === 0 ) calcRoundBlank(matrix, v)
    }
  }
 }

